import { useData } from "../hooks/datahook";
import { Nav, Navbar } from "react-bootstrap";
import bigLogo from "../images/logos/TypeChat.svg";
import smallLogo from "../images/logos/TS.svg";
import "bootstrap/dist/css/bootstrap.min.css";
import "./css/nav.css";
import cookies from "../cookies";
import useWindowSize from "../hooks/usescreensize";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faComment,
  faUserFriends,
  faPlus,
  faSignOutAlt,
  faCog,
} from "@fortawesome/free-solid-svg-icons";
import { Link, useLocation } from "react-router-dom";
import { useEffect, useRef } from "react";
import useComponentSize from "@rehooks/component-size";
import playSound from "../playsound";
const PageNav = () => {
  const { width } = useWindowSize();
  const Navbarref = useRef(null);
  const navbarsize = useComponentSize(Navbarref);
  const { loggedin, user, rechecklogged, setnavbarsize, chattingto } =
    useData();
  useEffect(() => {
    setnavbarsize(navbarsize);
  }, [navbarsize, setnavbarsize]);
  const location = useLocation();
  return (
    <>
      <Navbar ref={Navbarref} bg="darkpurple" variant="dark" fixed="top">
        <Navbar.Brand
          onPointerDown={() => {
            playSound("/sounds/click2.mp3");
          }}
          onPointerUp={() => {
            playSound("/sounds/click1.mp3");
          }}
          as={Link}
          to={loggedin ? "/contacts" : "/"}
        >
          <img
            alt="TypeChat"
            src={width > 700 ? bigLogo : smallLogo}
            style={{
              height: "40px",
            }}
          />{" "}
        </Navbar.Brand>

        <Nav className="mc-auto">
          {loggedin ? (
            <>
              {chattingto ? (
                <Nav.Link
                  onPointerDown={() => {
                    playSound("/sounds/click2.mp3");
                  }}
                  onPointerUp={() => {
                    playSound("/sounds/click1.mp3");
                  }}
                  as={Link}
                  to={`/chat/${chattingto}`}
                  style={{
                    color:
                      location.pathname === `/chat/${chattingto}`
                        ? "white"
                        : "",
                  }}
                >
                  <FontAwesomeIcon icon={faComment} />
                </Nav.Link>
              ) : (
                <></>
              )}
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                as={Link}
                to="/contacts"
                style={{
                  color: location.pathname === "/contacts" ? "white" : "",
                }}
              >
                <FontAwesomeIcon icon={faUserFriends} />
              </Nav.Link>
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                as={Link}
                to="/add"
                style={{ color: location.pathname === "/add" ? "white" : "" }}
              >
                <FontAwesomeIcon icon={faPlus} />
              </Nav.Link>
            </>
          ) : (
            <></>
          )}
        </Nav>
        <Nav>
          <Nav.Link
            onPointerDown={() => {
              playSound("/sounds/click2.mp3");
            }}
            onPointerUp={() => {
              playSound("/sounds/click1.mp3");
            }}
            as={Link}
            to="/settings"
            style={{ color: location.pathname === "/settings" ? "white" : "" }}
          >
            <FontAwesomeIcon icon={faCog} />
          </Nav.Link>
          {loggedin ? (
            <>
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                as={Link}
                to="/user/settings"
                style={{
                  color: location.pathname === "/user/settings" ? "white" : "",
                  textOverflow: "ellipsis",
                  maxWidth: "30vw",
                  overflow: "hidden",
                  whiteSpace: "nowrap",
                  padding: "0",
                }}
              >
                <img
                  alt="Profile"
                  src={"/files/" + user.profilePic}
                  style={{ height: "40px", borderRadius: "50%" }}
                />
              </Nav.Link>
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                onClick={async () => {
                  await fetch("/api/logout");
                  cookies.remove("token");
                  rechecklogged();
                }}
              >
                <FontAwesomeIcon icon={faSignOutAlt} />
              </Nav.Link>
            </>
          ) : (
            <>
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                as={Link}
                to="/login"
                style={{ color: location.pathname === "/login" ? "white" : "" }}
              >
                Login
              </Nav.Link>
              <Nav.Link
                onPointerDown={() => {
                  playSound("/sounds/click2.mp3");
                }}
                onPointerUp={() => {
                  playSound("/sounds/click1.mp3");
                }}
                as={Link}
                to="/signup"
                style={{
                  color: location.pathname === "/signup" ? "white" : "",
                }}
              >
                Sign Up
              </Nav.Link>
            </>
          )}
        </Nav>
      </Navbar>
      <div style={{ height: navbarsize.height }}></div>
    </>
  );
};
export default PageNav;
